body {
  background-color: #f8f8f8; }

@media screen and (min-width: 1120px) {
  body {
    display: flex; }
  section {
    position: absolute;
    left: 320px; } }

.container {
  max-width: 1440px;
  padding: 0 16px;
  box-sizing: border-box;
  margin: 0 auto; }
  @media screen and (min-width: 768px) {
    .container {
      padding: 0 24px; } }
  @media screen and (min-width: 1120px) {
    .container {
      padding: 0 32px; } }

.title {
  font-family: "TT Lakes Bold", sans-serif;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin: 0; }
  .title::before {
    content: ' ';
    position: absolute;
    left: 0;
    background: #41f6d7;
    width: 4px;
    height: 40px;
    border-radius: 0 100% 100% 0; }

.sub__title {
  font-family: "TT Lakes Regular", sans-serif;
  font-size: 14px;
  line-height: line-height;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7e7e82; }

.text {
  font-family: "TT Lakes Medium", sans-serif;
  font-size: 14px;
  line-height: line-height;
  letter-spacing: -0.2px;
  color: black; }

.hidden {
  display: none; }

@media screen and (min-width: 650px) {
  .hidden {
    display: block; } }

.main {
  background: #f8f8f8; }

.prices-row__items {
  height: auto; }

.prices__item {
  width: 100%; }
  .prices__item__height {
    width: 260px !important;
    height: 200px; }
    @media screen and (min-width: 768px) {
      .prices__item__height {
        width: 100% !important;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content; } }
  .prices__item__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;
    padding-top: 0;
    border: none; }
    .prices__item__content:hover {
      border: none; }
    @media screen and (min-width: 768px) {
      .prices__item__content:last-child {
        border-bottom: none;
        padding-bottom: 0; } }
  .prices__item__inner {
    display: flex;
    align-items: flex-end;
    justify-content: space-between; }
  .prices__item__title {
    font-family: "TT Lakes Medium", sans-serif;
    font-size: 14px;
    line-height: 16px;
    color: #7e7e82;
    opacity: 0.72; }
  .prices__item__text {
    font-family: "TT Lakes Medium", sans-serif;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: black; }
    @media screen and (min-width: 768px) {
      .prices__item__text {
        white-space: nowrap; } }
  .prices__item__wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 23px 0;
    border-bottom: 1px solid #eaeaea; }

.price__titles__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  margin-bottom: 16px; }

.price__description__wrapper {
  max-width: 348px;
  width: 100%; }

.price__price__wrapper {
  max-width: 75px;
  width: 100%; }

.price__term__wrapper {
  max-width: 80px;
  width: 100%; }

.price__btn__wrapper {
  max-width: 120px;
  width: 100%; }

hr {
  width: 100%;
  height: 1px;
  background: #eaeaea;
  margin: 23px 0;
  border: none; }

.commercial-offer {
  margin: 0 16px; }
  @media screen and (min-width: 768px) {
    .commercial-offer {
      width: 420px;
      margin: 0 3%; } }
  .commercial-offer__text1 {
    font-family: "TT Lakes Regular", sans-serif;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: black; }
  .commercial-offer__btn {
    width: 95%;
    text-align: left;
    border: none;
    background: none;
    outline: none;
    margin-top: 16px;
    margin-bottom: 40px;
    font-family: "TT Lakes Medium", sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: black; }
    .commercial-offer__btn svg {
      margin-bottom: -7px; }
