@font-face {
  font-family: 'TT Lakes Regular';
  src: url(./fonts/TTLakes-Regular.woff2), url(./fonts/TTLakes-Regular.woff);
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes Medium';
  src: url(./fonts/TTLakes-Medium.woff2), url(./fonts/TTLakes-Medium.woff);
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes Bold';
  src: url(./fonts/TTLakes-Bold.woff2), url(./fonts/TTLakes-Bold.woff);
  font-weight: normal;
  font-style: normal; }

body {
  background-color: #f8f8f8; }

@media screen and (min-width: 1120px) {
  body {
    display: flex; }
  section {
    position: absolute;
    left: 320px; } }

.container {
  max-width: 1440px;
  padding: 0 16px;
  box-sizing: border-box;
  margin: 0 auto; }
  @media screen and (min-width: 768px) {
    .container {
      padding: 0 24px; } }
  @media screen and (min-width: 1120px) {
    .container {
      padding: 0 32px; } }

.title {
  font-family: "TT Lakes Bold", sans-serif;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin: 0; }
  .title::before {
    content: ' ';
    position: absolute;
    left: 0;
    background: #41f6d7;
    width: 4px;
    height: 40px;
    border-radius: 0 100% 100% 0; }

.sub__title {
  font-family: "TT Lakes Regular", sans-serif;
  font-size: 14px;
  line-height: line-height;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7e7e82; }

.text {
  font-family: "TT Lakes Medium", sans-serif;
  font-size: 14px;
  line-height: line-height;
  letter-spacing: -0.2px;
  color: black; }

.hidden {
  display: none; }

@media screen and (min-width: 650px) {
  .hidden {
    display: block; } }

.main {
  background: #f8f8f8; }

.read-more {
  display: none;
  justify-content: start;
  align-items: flex-start;
  cursor: pointer; }
  @media screen and (min-width: 768px) {
    .read-more {
      display: flex; } }
  .read-more__btn {
    font-family: "TT Lakes Medium", sans-serif;
    font-size: 14px;
    line-height: line-height;
    letter-spacing: -0.2px;
    color: black;
    margin-bottom: 16px;
    padding: 0;
    outline: none;
    border: none;
    background: none;
    cursor: pointer; }
  .read-more svg {
    margin-top: -5px; }

.description__read-more {
  display: flex; }
